<template>
    <div class="ace">
        <v-btn
        class="gap"
        elevation="2"
        raised
        small
        @click="openSearchBox"
        ><v-icon>mdi-magnify-expand</v-icon></v-btn>
        <v-btn
        class="gap"
        elevation="2"
        raised
        small
        @click="openReplaceBox"
        ><v-icon>mdi-wrench-cog-outline</v-icon></v-btn>
        <el-col :span="18" class="gap">
            <el-dropdown trigger="click">
            <span class="el-dropdown-link">
                <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
                <div
                    @click="updateEditorTheme('ace/theme/github')">
                    <el-dropdown-item><v-icon>mdi-github</v-icon>GitHub</el-dropdown-item>
                </div>
                <div 
                    @click="updateEditorTheme('ace/theme/kuroir')">
                    <el-dropdown-item><v-icon>mdi-atom</v-icon>Kuroir</el-dropdown-item>
                </div>
                <div
                    @click="updateEditorTheme('ace/theme/one_dark')">
                    <el-dropdown-item><v-icon>mdi-brightness-4</v-icon>Dark</el-dropdown-item>
                </div>
            </el-dropdown-menu>
            </el-dropdown>
        </el-col>
      <div ref="ace" class="ace-editor"/>
        <v-btn
            class="gap-2"
            elevation="2"
            raised
            small
            color="#8BC34A"
            @click="getCode"
            >
            <v-icon>mdi-file-upload-outline</v-icon>
        </v-btn>
        <v-btn
            class="gap-2"
            elevation="2"
            raised
            small
            color="#B3E5FC"
            @click="setCode"
            >
            <v-icon>mdi-update</v-icon>
        </v-btn>
    </div>
</template>

<script>
//ace-editor
import ace from 'ace-builds'
// ace
import 'ace-builds/src-min-noconflict/theme-kuroir'
import 'ace-builds/src-min-noconflict/theme-one_dark'
import 'ace-builds/src-min-noconflict/theme-github'
// ace 
import 'ace-builds/src-min-noconflict/ext-searchbox'
// ace
import 'ace-builds/src-min-noconflict/mode-javascript'
import 'ace-builds/src-min-noconflict/mode-xml'
import 'ace-builds/src-min-noconflict/mode-json5'
//
import 'ace-builds/src-min-noconflict/ext-language_tools'
import { mapState } from 'vuex'

export default {
    name: 'AceDemoJSON',
    data() {
        return {
            editor: null,
            obj: null,
            event: function(){},
            eventobj: {
                "fn1": function() {
                    console.log
                }
            }
        }
    },
    computed: mapState([
        'canvasStyleData',
        'curComponent',
    ]),
    watch: {
        'curComponent': function() {
            this.setCode()
        },
        'canvasStyleData':function() {
            this.setCode()
        },
    },

    mounted() {
        ace.config.set("basePath", "https://cdnjs.cloudflare.com/ajax/libs/ace/1.4.14/");
        //-demo
            this.editor = ace.edit(this.$refs.ace, {
            maxLines: 36, // 
            minLines: 36, // 
            fontSize: 14, // 
            theme: 'ace/theme/github', // 
            mode: 'ace/mode/json5', // 
            tabSize: 4,//  4 
            readOnly: false,//
            enableBasicAutocompletion: true, //boolea  completer,
            enableLiveAutocompletion: true, //boolean  completer,
            enableSnippets: true // boolean
        });

        this.obj = this.curComponent || this.canvasStyleData
        this.editor.setValue(JSON.stringify(this.obj, null, 4))
    },
    methods: {
        setCode() {
            this.obj = this.curComponent || this.canvasStyleData
            this.editor.setValue(JSON.stringify(this.obj, null, 4))
        },

        getCode() {
            let str = this.editor.getValue()
            if (!this.curComponent)
                this.$store.commit('acesetCanvasData', JSON.parse(str))
            else
                this.$store.commit('acesetcurComponent', JSON.parse(str))
        },
        
        openSearchBox: function () {
            this.editor.execCommand('find');
        },
        openReplaceBox: function () {
            this.editor.execCommand('replace');
        },
        updateEditorTheme: function (theme) {
            this.editor.setTheme(theme);
        },
    }
}
</script>

<style scoped>
.ace-editor {
    margin-bottom: 7px;
}

.gap {
    margin: 7px;
}

.el-dropdown-link {
    cursor: pointer;
    color: #409EFF;
}

.el-icon-arrow-down {
    font-size: 12px;
}
.demonstration {
    display: block;
    color: #8492a6;
    font-size: 14px;
    margin: 10px;
}

.gap-2 {
    float: right;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: -2px;
}

</style>
